.describe {
    position: relative;
    width: 100vw;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1em;

    &__box {
        position: relative;
        width: 80%;
        height: 100%;
        &-text {
            font-size: 2.3rem;
            text-align: justify;
        }
    }
}

@media (min-width: 576px) {
    .describe {
        &__box {
            &-text {
                font-size: 3rem;
            }
        }
    }
}